import { Injectable } from '@angular/core';

@Injectable()
export class PageInfoService {
  route: string;
  dataset: Object = {
    'info': {
      'title': ' ',
      'description': ' ,   ',
      'report': {
        'title': '',
      },
      'news': {
        'title': ' '
      },
      'law': {
        'title': '  '
      },
      'weekly': {
        'title': ' '
      }
    },
    'site': {
      'title': ' ',
      'description': ', ,    ',
      'apartment': {
        'title': ''
      },
      'officetel': {
        'title': ''
      },
      'commercial': {
        'title': '/'
      },
      'ground': {
        'title': ''
      }
    },
    'recruit': {
      'title': ' ',
      'description': '  ',
      'employer': {
        'title': ' '
      },
      'employee': {
        'title': ' '
      }
    },
    'meeting': {
      'title': ' ',
      'description': '   !',
    },
    'search': {
      'title': ' '
    }
  };

  constructor() {
  }


  getCurrentData(routePath, data) {
    if (!routePath) { return; }
    const path = routePath.split('/');
    // console.log('executing...', path, path.length);

    let currentData;

    try {
      if (path.length < 2) {
        return 0;
      } else if (path.length === 2) {
        currentData = this.dataset[path[1]];
      } else {
        currentData = this.dataset[path[1]][path[2]];
      }
      return currentData[data];
    } catch (e) {
      return 0;
    }
  }
}



