    FormComponent.prototype.createUser = function (userInfo) {
        var _this = this;
        this.userService.create(this.user)
            .subscribe(function (data) {
            console.log('data: ', data);
            if (data.success) {
                _this.router.navigate(['signup', 'done']);
            }
            else {
                _this.alertService.error(data.error);
            }
        }, function (error) {
            _this.alertService.error('   .');
            console.log('error: ', error);
        });
    };
    FormComponent.prototype.payFee = function (userInfo) {
        var _this = this;
        // IMP.request_pay(param, callback) 
        console.log(userInfo);
        window['IMP'].request_pay({
            pg: 'kcp',
            pay_method: 'card',
            merchant_uid: 'WE' + new Date(),
            name: '' + userInfo['level'],
            amount: this.levelPrice[userInfo['level']],
            buyer_email: userInfo['email'],
            buyer_name: userInfo['name'],
            buyer_tel: userInfo['cp'],
            buyer_addr: userInfo['ha'],
        }, function (rsp) {
            if (rsp.success) {
                // this.info('success!');
                var now = new Date();
                var current = void 0;
                if (now.getMonth() === 11) {
                    current = new Date(now.getFullYear() + 1, 0, 1);
                }
                else {
                    current = new Date(now.getFullYear(), now.getMonth() + 1, 1);
                }
                userInfo['level-start'] = now;
                userInfo['level-end'] = current;
            }
            else {
                _this.alertService.warn('.');
                console.log(rsp);
            }
        });
    };
