import { OnInit, ElementRef, ViewContainerRef } from '@angular/core';
import { environment } from '../../../../environments/environment';
import { FbShareService } from '../../services/fb-share.service';
import { AlertService } from '../../services/alert.service';
import { UserService } from '../../services/user.service';
import { AuthService } from '../../services/auth.service';
var MeetingCardComponent = /** @class */ (function () {
    function MeetingCardComponent(_elementRef, viewContainerRef, fbShareService, alertService, userService, authService) {
        this._elementRef = _elementRef;
        this.viewContainerRef = viewContainerRef;
        this.fbShareService = fbShareService;
        this.alertService = alertService;
        this.userService = userService;
        this.authService = authService;
        this.level = 'standard';
    }
    MeetingCardComponent.prototype.ngOnInit = function () {
        this.imageUrl = environment.bucket.downloadUrl + this.record['master-image'];
        this._elementRef.nativeElement.classList.add(this.record['level'].toLowerCase());
    };
    MeetingCardComponent.prototype.formatDate = function (dateStr) {
        return dateStr.slice(0, 10).replace(/\-/gi, '.');
    };
    MeetingCardComponent.prototype.isSelf = function () {
        if (window.location.pathname === '/meeting/detail/' + this.record['no']) {
            this.alertService.info('   .');
        }
    };
    MeetingCardComponent.prototype.bookmark = function () {
        if (!this.authService.isAuthenticated()) {
            this.alertService.error('     .');
        }
        var bookmark = this._elementRef.nativeElement.querySelector('#bookmark');
        if (bookmark.classList.contains('selected')) {
            bookmark.src = '/assets/img/icon_bookmark.png';
            bookmark.classList.remove('selected');
            this.alertService.warn(' .');
            this.userService.removeBookmark(this.record);
        }
        else {
            bookmark.src = '/assets/img/icon_bookmark_selected.png';
            bookmark.classList.add('selected');
            this.alertService.success(' .');
            this.userService.addBookmark(this.record);
        }
    };
    MeetingCardComponent.prototype.fbShare = function () {
        this.fbShareService.share(environment.homeUrl + '/info/report');
    };
    return MeetingCardComponent;
}());
export { MeetingCardComponent };



