var PageInfoService = /** @class */ (function () {
    function PageInfoService() {
        this.dataset = {
            'info': {
                'title': ' ',
                'description': ' ,   ',
                'report': {
                    'title': '',
                },
                'news': {
                    'title': ' '
                },
                'law': {
                    'title': '  '
                },
                'weekly': {
                    'title': ' '
                }
            },
            'site': {
                'title': ' ',
                'description': ', ,    ',
                'apartment': {
                    'title': ''
                },
                'officetel': {
                    'title': ''
                },
                'commercial': {
                    'title': '/'
                },
                'ground': {
                    'title': ''
                }
            },
            'recruit': {
                'title': ' ',
                'description': '  ',
                'employer': {
                    'title': ' '
                },
                'employee': {
                    'title': ' '
                }
            },
            'meeting': {
                'title': ' ',
                'description': '   !',
            },
            'search': {
                'title': ' '
            }
        };
    }
    PageInfoService.prototype.getCurrentData = function (routePath, data) {
        if (!routePath) {
            return;
        }
        var path = routePath.split('/');
        // console.log('executing...', path, path.length);
        var currentData;
        try {
            if (path.length < 2) {
                return 0;
            }
            else if (path.length === 2) {
                currentData = this.dataset[path[1]];
            }
            else {
                currentData = this.dataset[path[1]][path[2]];
            }
            return currentData[data];
        }
        catch (e) {
            return 0;
        }
    };
    return PageInfoService;
}());
export { PageInfoService };



