import { Component, OnInit } from '@angular/core';
import { String } from 'aws-sdk/clients/ecs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  selectedName: String = ' ';
  menuTree: Array<Object> = [
    {
      'path': 'info',
      'name': ' ',
      'main': 'recent',
      'sub': [
        {
          'path': 'report',
          'name': '',
        },
        {
          'path': 'news',
          'name': ' ',
        },
        {
          'path': 'law',
          'name': '  '
        },
        {
          'path': 'weekly',
          'name': ' '
        },
      ]
    },
    {
      'path': 'site',
      'name': ' ',
      'main': 'apartment',
      'sub': [
        {
          'path': 'apartment',
          'name': ''
        },
        {
          'path': 'officetel',
          'name': ''
        },
        {
          'path': 'commercial',
          'name': '/'
        },
        {
          'path': 'ground',
          'name': ''
        }
      ]
    },
    {
      'path': 'recruit',
      'name': ' ',
      'main': 'employer',
      'sub': [
        {
          'path': 'employer',
          'name': ''
        },
        {
          'path': 'employee',
          'name': ''
        },
        {
          'path': 'apply',
          'name': ' '
        },
      ],
    },
    {
      'path': 'meeting',
      'name': ' ',
      'main': 'meeting'
    },
    {
      'path': 'portfolio',
      'name': ' ',
      'main': 'portfolio'
    }
  ];

  constructor() { }

  ngOnInit() {
  }

  viewSubMenu(name, event) {
    this.selectedName = name;
  }

  hideSubMenu(name) {
    this.selectedName = undefined;
  }
}



